"use client";
import React from "react";

interface Player {
  id: string;
  name: string;
  amount: number;
  isWinner?: boolean;
}

interface SlotMachineProps {
  players: Player[];
  currentWinnerId?: string | null;
  isSpinning: boolean;
}

const SlotMachine: React.FC<SlotMachineProps> = ({
  players,
  currentWinnerId,
  isSpinning,
}) => {
  // Select current player (winner or fallback)
  const currentPlayer =
    players.find((p) => p.id === currentWinnerId) || players[0];

  return (
    <div className="relative w-full max-w-3xl mx-auto p-4">
      {/* Slot machine container */}
      <div className="relative flex flex-col items-center">
        
        {/* Slot machine image */}
        <div className="relative flex justify-center items-center mb-4">
          <img
            src="/images/slot_machine.png"
            alt="Slot Machine"
            className="drop-shadow-lg select-none pointer-events-none max-w-[638px] h-auto"
          />

          {/* Reels with names displayed vertically */}
          <div
            className={`absolute inset-0 flex flex-col items-center justify-center space-y-4 ${
              isSpinning ? "animate-pulse" : ""
            }`}
          >
            {players.map((player, index) => (
              <div
                key={index}
                className={`px-6 py-2 text-center bg-white text-[#4E2A0B] font-bold text-lg rounded-lg border-2 border-[#F7A531] ${
                  currentPlayer.id === player.id ? "text-[#F7A531] border-[#F7A531]" : ""
                }`}
              >
                {player.name.toUpperCase()}
              </div>
            ))}
          </div>
        </div>

        {/* Winner Display */}
        <div className="text-center mt-4">
          {currentWinnerId && (
            <div className="inline-block bg-gradient-to-r from-[#FFD85A] to-[#F7A531] text-[#4E2A0B] font-extrabold py-2 px-6 rounded-full shadow">
              üèÜ Winner:{" "}
              {players.find((p) => p.id === currentWinnerId)?.name || "Unknown"} üèÜ
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default SlotMachine;
